.station-header-main-container {

    @include for-mobile-layout {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.station-header-details-container {
    display: flex;
    flex-direction: column;

    h5 {
        margin: 30px 0 0;
    }
}

.station-header-details-container-2 {
    font-size: 0.80em;

    margin-top: 10px;

    &.station-user-name {
        text-transform: capitalize;
    }

    @include for-mobile-layout {
        font-size: 0.60em;
        color: rgb(172, 170, 170);
    }
}

.station-header-name-container {
    @include for-normal-layout {
        font-size: 2.75rem;
        margin: 0;
    }

    @include for-wide-layout {
        text-indent: -6px;
        margin-inline-start: 0;
        font-size: 5.5rem;
        letter-spacing: -5px;
    }


    @include for-mobile-layout {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 1.4rem;
        letter-spacing: -1px;
        text-indent: -2px;
        width: 85%;
        margin-top: 0;
        position: absolute;
        top: 245px;
        left: 10px;
    }
}

.station-img {
    width: rem(270px);
    height: 276px;
    padding: 30px;

    @include for-mobile-layout {
        height: 230px;
        width: 230px;
        padding: 10px;

    }
}

.admin-state-btn {
    font-size: 32px;
    cursor: pointer;
}


.station-user-name {
    font-weight: bold;
    font-size: 14px;
}

.playlist-btns {
    display: flex;
    gap: 35px;
    height: 150px;
    align-items: center;
    padding: 30px;

    @include for-mobile-layout {
        height: 65px;
        gap: 20px;
        justify-content: space-between;
        padding: 14px;
        flex-direction: row-reverse;
    }

    .play-btn {
        border-radius: 50%;
        border: none;
        transform: scale(1.1);
        background: #1ed760;
        transition: 0.4s;
        width: 3.125rem;
        height: 3.125rem;
        font-size: 1.2rem;

        @include for-mobile-layout {
            width: 2.75rem;
            height: 2.75rem;
            font-size: 1rem;
        }
    }

    & svg {
        height: 32px;
    }

    & .fa-pause {
        display: none;
    }

    & .equalizer-img {
        width: 3.125rem;
        height: 3.125rem;
    }

    .play-btn-container {

        &:hover {
            & .equalizer {
                display: none;
            }

            & .play-btn.fa-pause {
                display: unset;
            }
        }
    }

}
.station-header .dropdown {
    top: 25%;
}